.login-form-input-wrapper label {
  display: block;
  margin: 5px 0px;
}

.login-form-input {
  width: 100%;
  border: 0px;
  border-bottom: 1px solid #1242bd;
}

.login-form-input-label {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  color: #999999;
}

.login-form-input-space {
  margin-bottom: auto;
}

.login-form-button {
  width: 100%;
  height: 37px;
  border-radius: 8px;
  color: #ffffff;
  background: #107d9f;
  border-radius: 8px;
  border-width: 0px;
}